.asidebar {
  &__head {
    margin: 30px 0px;
    font-size: 28px;
    font-weight: 800;
  }

  width: 100%;
  &__row {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    gap: 10px;
    @media (max-width: 867px) {
      justify-content: center;
    }
  
  }
  &__box {
    padding: 15px 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 270px;
    height: 170px;
    background-color: $backLight;
    border-radius: 20px;
    font-weight: 700;
  }
  &__temp--pm{
    padding: 4px 0px;
  }
  &__title {
    font-size: 20px;
    font-weight: 700;
  }
}
